import React from 'react';

import type { Suspect } from '@jan6evidence/types';

import PageHeaderAndNav from './PageHeaderAndNav';
import SuspectsGallery from './SuspectsGallery';
import SuspectsTable from './SuspectsTable';
import { usePageTitle } from './shared/hooks';
import { AppWrapper } from './table-styled-components';

interface SuspectsAppProps {
  suspects: Suspect[];
}

const SuspectsApp = (props: SuspectsAppProps) => {
  usePageTitle('January 6 Persons of Interest');

  return (
    <AppWrapper>
      <PageHeaderAndNav />
      <div style={{ margin: '30px 30px 0px', maxWidth: '800px' }}>
        <h2>Persons-of-Interest Gallery</h2>
        <p style={{ fontSize: '0.9rem', marginBottom: '0px' }}>
          Data from the{' '}
          <a href="https://docs.google.com/spreadsheets/d/18NzncPJ-5oaYrDfq_VnjaSTLyY_vKaoSTgc6lznl9GU/edit#gid=0">
            Capitol Suspects Database
          </a>
          .
        </p>
      </div>
      <SuspectsGallery suspects={props.suspects} />
    </AppWrapper>
  );
};

export const SuspectsTableApp = (props: SuspectsAppProps) => {
  usePageTitle('January 6 Persons of Interest');

  return (
    <AppWrapper>
      <PageHeaderAndNav />
      <SuspectsTable suspects={props.suspects} />
    </AppWrapper>
  );
};

export default SuspectsApp;
